* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

#root {
  background-color: #f6f8fa;
  min-height: 100vh;
  color: #24292e;
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-flow: column;
  align-items: center;
}

button {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background-color: #f6f8fa;
  color: #24292e;
  font-size: 1rem;
  cursor: pointer;
  border: 0.25rem solid transparent;
}

button:hover {
  border: 0.25rem solid #505458;
}

button[disabled] {
  border: 0.25rem solid transparent;
  cursor: not-allowed;
  background-color: #94999e;
}

